<template>
	<div class="model3">
		<button @click="ifshow=!ifshow" class="btn btn-success">
				显示、影藏{{ifshow}}
			</button>
		<div class="box" v-if="ifshow">
			我是div的if
		</div>

		<button @click="show=!show" class="btn btn-success">
				显示、影藏{{show}}
			</button>
		<div class="box" v-if="show">
			我是div的show
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				ifshow: true,
				show: false
			}
		}
	}
</script>

<style>
	.box {
		width: 300px;
		height: 300px;
		background: red;
	}
</style>


