<template>
	<div class="model2">
		<div class="panel panel-default">
			<div class="panel-heading">
				<h3 class="panel-title">000000000({{total}})</h3>
			</div>
			<div class="panel-body">
				<ul class="list-group">
					<li :class="{done:v.completed}" v-for="(v,index) in todos" class="list-group-item">
						{{v.name}}{{v.completed}}
						<button :class="v.completed?'btn-danger':'btn-success'" class="btn  pull-right btn-xs" @click="yyy(index)">{{v.completed?'完成':'已完成'}}</button>
						<button class="btn btn-info btn-xs pull-right" @click="ccc(index)">
							删除
						</button>
					</li>
				</ul>
				<input type="text" class="form-control" v-model="newData.name" name="" id="" />
				<button @click="addNews(newData)" class="btn btn-success">tiajia </button>
			</div>
			<div class="panel-footer">

			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				newData: {
					id: null,
					title: '',
					completed: false

				},
				todos: [{
						id: 1,
						name: "我去4",
						completed: false
					},
					{
						id: 2,
						name: "我去3",
						completed: false
					},
					{
						id: 3,
						name: "我去4",
						completed: false
					},
					{
						id: 4,
						name: "我去4",
						completed: false
					},
					{
						id: 5,
						name: "我去5",
						completed: false
					}
				]
			}
		},
		methods: {
			addNews(data) {
				this.todos.push(data);
				this.newData = {
					id: null,
					title: ''
				}
				console.log(this.todos)
			},
			yyy(index) {
				this.todos[index].completed = !this.todos[index].completed;
				console.log(index)
			},
			ccc(index) {
				this.todos.splice(index, 1)
			}
		},
		computed: {
			total() {
				return this.todos.length;
			}

		}
	}
</script>

<style>
	.done {
		color: red;
		text-decoration: line-through;
	}
</style>


